n  margin: -10px;\n}\n\n#current-condition {\n  margin-top: 0px;\n  font-size: 1.4rem;\n  color: var(--secondary-font-clr);\n}\n\n.all-temperatures {\n  margin-top: 10px;\n  display: grid;\n  grid-template-columns: 1fr 2fr;\n  grid-template-rows: 2fr 1fr;\n  grid-column-gap: 16px;\n}\n\n.all-temperatures .temperature {\n  font-size: 2.2rem;\n  font-weight: 800;\n}\n\n#min-max-temperatures {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n}\n\n.all-temperatures .max-temperature,\n.all-temperatures .min-temperature {\n  font-size: 1.2rem;\n  font-weight: 600;\n}\n\n.min-temperature {\n  color: var(--secondary-font-clr);\n}\n\n#feels-like-box {\n  font-size: 1.2rem;\n  grid-column: 1 / 3;\n}\n\n#date {\n  margin-top: 20px;\n  font-size: 1.15rem;\n  line-height: 1;\n\n  color: var(--secondary-font-clr);\n}\n\n#location {\n  font-size: 2rem;\n  line-height: 1.1;\n}\n\n#current-precipprob {\n  margin-top: 4px;\n  display: grid;\n  grid-template-columns: 3rem auto auto;\n  align-items: center;\n  justify-content: start;\n  grid-column-gap: 5px;\n  font-size: 1.2rem;\n}\n\n#current-precipprob > img {\n  aspect-ratio: 1 / 1;\n  margin: -4px;\n}\n\n#unit-switch {\n  width: fit-content;\n  margin-left: auto;\n}\n\n#unit-switch input[type=\"radio\"] {\n  appearance: none;\n}\n\n#unit-switch > label {\n  display: inline-flex;\n  justify-content: center;\n  align-items: center;\n\n  width: 40px;\n  height: 40px;\n  margin-right: 2px;\n  padding: auto;\n  font-size: 1.2rem;\n\n  border-radius: 100%;\n\n  &:has(input[type=\"radio\"]:checked) {\n    background-color: #292929;\n    color: var(--grey-bg-clr);\n  }\n\n  &:has(input[type=\"radio\"]:hover) {\n    border: 3px solid #292929;\n  }\n}\n\n#unit-switch span {\n  text-align: center;\n  padding-top: 3px;\n}\n\n.hourly-forecasts,\n.daily-forecasts,\n.today-highlights {\n  margin-top: 16px;\n\n  & > h1 {\n    font-size: 1.6rem;\n  }\n}\n\n#hourly-cards,\n#daily-cards {\n  display: flex;\n  overflow-x: auto;\n  white-space: nowrap;\n  padding-bottom: 5px;\n  gap: 16px;\n}\n\n.hourly-card,\n.daily-card {\n  background-color: var(--bright-bg-clr);\n  width: 10%;\n  min-width: 90px;\n  height: 150px;\n\n  display: inline-flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  text-align: center;\n\n  border-radius: var(--card-border-radius);\n  padding: 10px;\n}\n\n.hourly-card img.weather-icon,\n.daily-card img.weather-icon {\n  width: 60px;\n}\n\n.today-highlights > .cards {\n  display: grid;\n  grid-template-columns: repeat(3, minmax(160px, 1fr));\n  grid-template-rows: repeat(2, 250px);\n  margin-top: 6px;\n  gap: 1.6rem;\n  max-width: 1300px;\n}\n\n.info-card {\n  min-width: 160px;\n  padding: 1.6rem;\n  background-color: var(--bright-bg-clr);\n  border-radius: var(--card-border-radius);\n\n  display: flex;\n  flex-direction: column;\n}\n\n.info-card h2 {\n  color: var(--secondary-font-clr);\n  font-size: 1.3rem;\n}\n\n.info-data {\n  margin-top: auto;\n}\n\n.info-data span.number {\n  font-size: 2.4rem;\n  color: var(--primary-font-clr);\n  font-weight: 600;\n\n  & + span.unit {\n    font-size: 1.6rem;\n  }\n}\n\n.info-card .sub-data {\n  margin-top: 0;\n  margin-bottom: auto;\n  font-size: 1.4rem;\n}\n\n.info-card span {\n  font-size: inherit;\n}\n\n#wind-direction {\n  display: grid;\n  grid-template-columns: auto auto;\n  align-items: center;\n  justify-content: start;\n  gap: 5px;\n}\n\n#wind-direction > .info-icon {\n  background-image: url(./info-icons/compass.svg);\n  background-size: 120%;\n  background-repeat: no-repeat;\n  background-position: center;\n  width: 50px;\n  height: 50px;\n  margin-left: -7px;\n}\n\n#sunrise,\n#sunset {\n  height: 50%;\n  display: grid;\n  grid-auto-flow: column;\n  grid-template-columns: min-content auto;\n  align-items: center;\n  gap: 1rem;\n\n  & > img {\n    width: 75px;\n  }\n\n  & > span {\n    font-size: 2rem;\n    font-weight: bold;\n  }\n}\n\n@media only screen and (max-width: 1400px) {\n  .info-data span.number {\n    font-size: 2.6rem;\n  }\n\n  #sun span {\n    font-size: 1.6rem;\n  }\n\n  #sun img {\n    width: 60px;\n  }\n}\n\n@media only screen and (max-width: 1250px) {\n  #location {\n    font-size: 2rem;\n  }\n\n  .info-card {\n    padding: 1.6rem 1.2rem;\n  }\n\n  .today-highlights > .cards {\n    grid-template-rows: repeat(2, 250px);\n  }\n\n  .info-data span.number {\n    font-size: 2.2rem;\n\n    & + span.unit {\n      font-size: 1.6rem;\n    }\n  }\n\n  #sun span {\n    font-size: 1.4rem;\n  }\n\n  #sunrise img,\n  #sunset img {\n    width: 50px;\n  }\n}\n\n@media only screen and (max-width: 1110px) {\n  .info-card h2 {\n    font-size: 1.3rem;\n  }\n\n  .info-data span.number {\n    font-size: 2rem;\n\n    & + span.unit {\n      font-size: 1.4rem;\n    }\n  }\n}\n\n@media only screen and (max-width: 1065px) {\n  .current-weather {\n    padding-inline: 24px;\n  }\n\n  .all-temperatures .temperature {\n    font-size: 2rem;\n  }\n\n  .all-temperatures .max-temperature,\n  .all-temperatures .min-temperature {\n    font-size: 1.1rem;\n  }\n\n  .today-highlights > .cards {\n    grid-template-columns: repeat(2, 1fr);\n    grid-template-rows: repeat(3, 250px);\n  }\n\n  .info-data span.number {\n    font-size: 2.2rem;\n\n    & + span.unit {\n      font-size: 1.6rem;\n    }\n  }\n}\n\n@media only screen and (max-width: 880px) {\n  .today-highlights > .cards {\n    grid-template-columns: repeat(2, 1fr);\n    grid-template-rows: repeat(4, 250px);\n  }\n\n  .info-card#uv {\n    grid-column: 1 / 3;\n  }\n}\n\n@media only screen and (max-width: 810px) {\n  body {\n    grid-template-columns: calc(185px + 3vw) minmax(0, 1fr);\n  }\n\n  .current-weather #date {\n    font-size: 1.1rem;\n  }\n\n  .current-weather #location {\n    font-size: 1.6rem;\n  }\n\n  .all-temperatures {\n    display: block;\n  }\n\n  #current-precipprob {\n    grid-template-columns: 2.3rem auto auto;\n    font-size: 1.15rem;\n  }\n\n  .detailed-forecasts {\n    padding-inline: 36px;\n  }\n\n  .detailed-forecasts h1 {\n    font-size: 1.6rem;\n  }\n\n  h2.description {\n    font-size: 1.4rem;\n  }\n}\n\n@media only screen and (max-width: 700px) {\n  .today-highlights > .cards {\n    grid-template-rows: 250px repeat(3, 200px);\n  }\n\n  .info-card h2 {\n    font-size: 1.2rem;\n  }\n\n  .info-data span.number {\n    font-size: 1.8rem;\n\n    & + span.unit {\n      font-size: 1.4rem;\n    }\n  }\n\n  .info-card .sub-data {\n    font-size: 1.35rem;\n  }\n\n  #sunset img,\n  #sunrise img {\n    width: 40px;\n  }\n\n  #sun span {\n    font-size: 1.2rem;\n  }\n}\n\n/*Mobile size*/\n@media only screen and (max-width: 650px) {\n  body {\n    width: 100vw;\n    grid-template-columns: 100%;\n    grid-template-rows: auto auto;\n  }\n\n  .current-weather {\n    text-align: center;\n    padding-bottom: 16px;\n    padding-top: 24px;\n  }\n\n  .current-weather #location {\n    font-size: 2rem;\n  }\n\n  img#current-weather-icon {\n    width: 60%;\n    margin: 0 auto;\n    margin-top: -5vw;\n    margin-bottom: -3vw;\n  }\n\n  .all-temperatures {\n    margin-top: 5px;\n  }\n\n  #min-max-temperatures {\n    flex-direction: row;\n    gap: 16px;\n  }\n\n  #current-precipprob {\n    width: max-content;\n    margin-inline: auto;\n  }\n\n  .detailed-forecasts {\n    padding: 0 24px;\n  }\n\n  .hourly-forecasts,\n  .daily-forecasts,\n  .today-highlights,\n  #unit-switch {\n    margin-top: 12px;\n  }\n\n  #hourly-cards,\n  #daily-cards {\n    display: flex;\n    align-items: center;\n  }\n\n  .today-highlights > .cards {\n    gap: 16px;\n  }\n}\n\n@media only screen and (max-width: 450px) {\n  .detailed-forecasts h1 {\n    font-size: 1.3rem;\n  }\n\n  .today-highlights > .cards {\n    grid-template-rows: 200px;\n    grid-auto-rows: 160px;\n  }\n\n  .info-card {\n    padding: 1rem 0.8rem;\n  }\n\n  h2.description {\n    font-size: 1.1rem;\n  }\n\n  .info-card h2 {\n    font-size: 1.1rem;\n  }\n\n  .info-data span.number {\n    font-size: 1.6rem;\n\n    & + span.unit {\n      font-size: 1.2rem;\n    }\n  }\n\n  .info-card .sub-data {\n    font-size: 1.1rem;\n  }\n\n  #sun span {\n    font-size: 1rem;\n  }\n\n  #hourly-cards,\n  #daily-cards {\n    gap: 12px;\n  }\n\n  .hourly-card,\n  .daily-card {\n    background-color: var(--bright-bg-clr);\n    width: 20%;\n    min-width: 70px;\n    height: 120px;\n  }\n\n  .hourly-card img.weather-icon,\n  .daily-card img.weather-icon {\n    width: 45px;\n  }\n\n  #unit-switch > label {\n    width: 32px;\n    height: 32px;\n    font-size: 1.05rem;\n  }\n\n  #unit-switch span {\n    padding-top: 0px;\n  }\n}\n"],"sourceRoot":""}]);
