#uv-chart {
  --section-width: 280px;
  --section-height: calc(var(--section-width) / 2);

  /*Offset the size of bar excluding everything else*/
  --size-offset: 72px;
  --chart-width: calc(var(--section-width) - var(--size-offset));
  --chart-height: calc(var(--section-height) - var(--size-offset) / 2);
  --animation-duration: 0.1s;

  --bar-thickness: 30px;

  position: relative;
  width: var(--section-width);
  height: var(--section-height);
  padding: 0;
  margin-inline: auto;
  margin-top: 16px;

  overflow: hidden;

  justify-self: center;
}

#uv-chart + .sub-data {
  text-align: center;
  margin: 0;
  margin-top: 14px;
  line-height: 0.5;
}

#uv-chart::before,
#uv-chart::after {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

/*The chart or bar*/
#uv-chart::before {
  content: "";
  width: var(--chart-width);
  height: var(--chart-height);
  border: var(--bar-thickness) rgba(0, 0, 0, 0.089) solid;
  border-bottom: none;

  /*Make it round*/
  border-top-left-radius: 1000px;
  border-top-right-radius: 1000px;
}

/*The UV index number in the center of chart*/
#uv-chart::after {
  content: attr(data-content);
  font-size: 2rem;
  font-weight: bold;
  line-height: 1;
  color: var(--primary-font-clr);
}

#uv-bar {
  position: absolute;
  top: 100%;
  left: calc(var(--size-offset) / 2);
  width: var(--chart-width);
  height: var(--chart-height);

  border: var(--bar-thickness) solid;
  border-top: none;
  border-bottom-left-radius: var(--chart-height);
  border-bottom-right-radius: var(--chart-height);
  border-color: #ffbd5f;

  transform-origin: 50% 0;

  transform: rotate(0);
  transition: transform 1.1s ease-in-out;
}

#uv-chart > .index {
  position: absolute;
  left: 0;
  top: 100%;
  width: inherit;
  height: inherit;
  transform-origin: 50% 0;
}

.index > span {
  position: absolute;
  font-size: 1.2rem;
  left: 17px;
  top: -15px;
  color: var(--secondary-font-clr);
}

#index-0 > span {
  top: -26px;
  left: 18px;
}

#index-3 {
  transform: rotate(54deg);

  & > span {
    transform: rotate(-54deg);
  }
}

#index-5 {
  transform: rotate(90deg);

  & > span {
    transform: rotate(-90deg);
  }
}

#index-7 {
  transform: rotate(126deg);

  & > span {
    transform: rotate(-126deg);
  }
}

#index-10 {
  transform: rotate(180deg);

  & > span {
    transform: rotate(-180deg);
    top: -6px;
    left: 10px;
  }
}

@media only screen and (max-width: 1375px) {
  #uv-chart {
    --section-width: 260px;
    --bar-thickness: 25px;
  }

  #uv-chart::after {
    font-size: 1.7rem;
  }

  #uv-chart + .sub-data {
    font-size: 1.4rem;
  }
}

@media only screen and (max-width: 1305px) {
  #uv-chart {
    --section-width: 250px;
    --size-offset: 60px;
    --bar-thickness: 25px;
  }

  .index > span {
    left: 12px;
  }

  #uv-chart::after {
    font-size: 1.4rem;
  }

  #uv-chart + .sub-data {
    font-size: 1.2rem;
  }

  #uv-chart .index {
    & > span {
      font-size: 1.1rem;
    }
  }

  #index-0 > span {
    top: -23px;
    left: 14px;
  }

  #index-10 > span {
    top: -5px;
    left: 8px;
  }
}

@media only screen and (max-width: 1200px) {
  #uv-chart {
    --section-width: 190px;
    --size-offset: 50px;
    --bar-thickness: 25px;
  }

  #uv-chart::after {
    font-size: 1.2rem;
  }

  #uv-chart + .sub-data {
    font-size: 1.2rem;
  }

  #index-0 > span {
    left: 12px;
  }

  #index-10 > span {
    left: 5px;
  }
}

@media only screen and (max-width: 1110px) {
  #uv-chart {
    --section-width: 175px;
    --size-offset: 50px;
    --bar-thickness: 20px;
